import {NgModule} from '@angular/core'
import {FormsModule} from '@angular/forms'
import {CommonModule} from '@angular/common'
import {WidgetsModule} from '../../widgets'
import {
  PedidosMainComponent,
  PedidosPanelComponent,
  PedidosRoutes,
  RegPedidosComponent,
  SeguimientoPedidosComponent
} from './'

@NgModule({
  imports: [
    FormsModule,
    CommonModule,
    WidgetsModule,
    PedidosRoutes
  ],
  declarations: [
    PedidosMainComponent,
    PedidosPanelComponent,
    SeguimientoPedidosComponent,
    RegPedidosComponent
  ]
})
export class PedidosModule {
  public static routes = PedidosRoutes
}



