import { Component, Output, EventEmitter } from '@angular/core'
import { Router } from '@angular/router'

@Component({
  selector:'seleccionar-cliente',
  templateUrl: './seleccion-cliente.component.html',
  styleUrls: ['./seleccion-cliente.component.scss']
})
export class SeleccionarClienteFormComponent {
  @Output() status = new EventEmitter()

  cities:any[]= [
    {id: 1, name: "Cortazar"},
    {id: 2, name: "Morelia"},
    {id: 3, name: "Huandacareo"},
    {id: 4, name: "Lazaro Cardenas"}
  ]

  clients:any [] = [
    {id:1, name: "Decasa"},
    {id:2, name: "Abc"},
    {id:3, name: "Soriana"},
    {id:4, name: "Mexicana"}
  ]

  getValueClient(value){
    console.log(value);
  }

  namesOrCompanies:any[] = [
    {id:1, name: "Juan Perez"},
    {id:2, name: "Abc" },
    {id:3, name: "Francisco Lopez"},
    {id:4, name: "El Mexicano"}
  ];
  getValueNameOrCompany(value){
    console.log(value);
  }

  products:any [] = [
    {id:1, name:"Henkel"},
    {id:2, name:"Salsa"},
    {id:3, name:"Xtreme"},
    {id:4, name:"El Mexicano"},
    {id:5, name:"Chocomilk"},
    {id:6, name:"Cerillos"},
    {id:7, name:"Jabon liquido de manos"},
    {id:8, name:"Cremas corporales"}
  ];

  getValueProduct(value){
    console.log(value)
  }

  constructor( private router:Router ) {}

  changeForm (form) {
    this.status.next(form)
  }

}



