import { Component } from '@angular/core'

@Component({
  template:`
    <div [ngSwitch]="status">
      <div *ngSwitchCase="1">
        <seleccionar-cliente (status)="siguient($event)"></seleccionar-cliente>
      </div>
      <div *ngSwitchCase="2">
        <registrar-pedido (status)="siguient($event)"></registrar-pedido>
      </div>
      <div *ngSwitchCase="3">
        <resumen-pedido (status)="siguient($event)"></resumen-pedido>
      </div>
    </div>
  `
})
export class RegPedidosComponent {
  status = 1

  constructor() {}

  siguient(form) {
    if (isNaN(form)) {
        console.log(form)
    } else {
      this.status = form
    }
  }
}



