import {RouterModule} from '@angular/router'
import {
  PedidosMainComponent,
  PedidosPanelComponent,
  SeguimientoPedidosComponent,
  RegPedidosComponent
} from './'

const routes = [{
  path: '',
  component: PedidosMainComponent,
  children: [{
    path: 'panel',
    component: PedidosPanelComponent
  }, {
    path: 'seguimiento',
    component: SeguimientoPedidosComponent
  }, {
    path: 'registrar',
    component: RegPedidosComponent
  }]
}]

export const PedidosRoutes = RouterModule.forChild(routes)



